.dashboard-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.dashboard-content {
  display: flex;
  flex: 1;
}

/* Logout button styles */
.logout-button {
  background: none;
  border: none;
  color: #6b7280;
  padding: 0.5rem;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logout-button:hover {
  background-color: #f3f4f6;
  color: #dc2626;
  transform: scale(1.05);
}

.logout-button:active {
  transform: scale(0.95);
}

.logout-button svg {
  width: 20px;
  height: 20px;
}

.dashboard-sidebar {
  width: 220px;
  background-color: #e5f0ff;
  padding: 1rem;
  border-left: 2px solid #ccc;
}

.sidebar-button {
  display: block;
  width: 100%;
  background-color: #1e40af;
  color: white;
  border: none;
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 0.75rem;
  font-size: 1rem;
  cursor: pointer;
}

.sidebar-button.active {
  background-color: #7c3aed;
}

.dashboard-main {
  flex: 1;
  padding: 2rem;
}

.books-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  gap: 1rem;
}

.book-item {
  background-color: white;
  padding: 1rem;
  border-radius: 1rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.book-image {
  width: 100%;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
}

.add-book-button {
  background-color: #1e40af;
  color: white;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 0.75rem;
  cursor: pointer;
  font-weight: bold;
  margin-bottom: 1rem;
}

.edit-button,
.delete-button {
  padding: 0.5rem;
  margin: 0.25rem;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
}

.edit-button {
  background-color: #facc15;
}

.delete-button {
  background-color: #d32f2f;
  color: white;
}

.hamburger-menu-btn {
  background: none;
  border: none;
  cursor: pointer;
  position: relative;
  margin-left: 1rem; /* changed from margin-right */
  display: flex;
  align-items: center;
}

.dashboard-hamburger-menu {
  position: absolute;
  top: 40px;
  right: 0;
  min-width: 180px;
  background: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  border-radius: 8px;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  padding: 0.5rem 0;
  font-size: 1rem;
}

.dashboard-hamburger-menu .menu-item {
  background: none;
  border: none;
  text-align: right;
  padding: 0.75rem 1.5rem;
  cursor: pointer;
  transition: background 0.2s;
  font-family: inherit;
  font-size: 1rem;
}

.dashboard-hamburger-menu .menu-item:hover:not(:disabled) {
  background: #f0f0f0;
}

.dashboard-hamburger-menu .menu-item.logout {
  color: #d32f2f;
  font-weight: bold;
}

.menu-item-disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Sidebar sub-buttons for orders */
.sidebar-sub-buttons {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-right: 1.5rem;
  margin-top: 0.3rem;
}
.sidebar-sub-button {
  background: #f3f4f6;
  color: #1e40af;
  border: none;
  border-radius: 6px;
  padding: 0.5rem 1.2rem;
  font-size: 1rem;
  font-weight: 500;
  text-align: right;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
  margin-bottom: 0.1rem;
}
.sidebar-sub-button.active,
.sidebar-sub-button:hover {
  background: linear-gradient(to right, #dbeafe, #a5b4fc);
  color: #312e81;
}
