/* Orders Table Styles */
.orders-table-wrapper {
  overflow-x: auto;
  margin-top: 2rem;
}
.orders-table {
  width: 100%;
  border-collapse: collapse;
  background: #fff;
  font-size: 1.05rem;
  min-width: 700px;
  box-shadow: 0 2px 8px rgba(60, 30, 90, 0.07);
}
.orders-table th,
.orders-table td {
  border: 1px solid #e5e7eb;
  padding: 0.75rem 1rem;
  text-align: right;
}
.orders-table th {
  background: #f3f4f6;
  color: #1e40af;
  font-weight: 700;
}
.orders-table tbody tr:nth-child(even) {
  background: #f9fafb;
}
.orders-table tbody tr:hover {
  background: #dbeafe;
}
.orders-title {
  margin-top: 1.5rem;
  color: #1e40af;
  font-size: 1.5rem;
  font-weight: bold;
  text-align: center;
}

.orders-loading,
.orders-error,
.orders-empty {
  text-align: center;
  margin-top: 2rem;
  font-size: 1.1rem;
  color: #c026d3;
}

/* Expand button */
.expand-btn {
  background: #e0e7ff;
  color: #1e40af;
  border: none;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  font-size: 1.3rem;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}
.expand-btn:hover {
  background: #a5b4fc;
  color: #312e81;
}

/* Nested order items table */
.order-items-table-wrapper {
  margin: 1rem 0 0.5rem 0;
  background: #f3f4f6;
  border-radius: 8px;
  padding: 1rem;
  box-shadow: 0 1px 4px rgba(60, 30, 90, 0.06);
}
.order-items-table {
  width: 100%;
  border-collapse: collapse;
  background: #fff;
  font-size: 1rem;
}
.order-items-table th,
.order-items-table td {
  border: 1px solid #e5e7eb;
  padding: 0.5rem 0.7rem;
  text-align: right;
}
.order-items-table th {
  background: #e0e7ff;
  color: #1e40af;
  font-weight: 600;
}
.order-items-table img.order-book-image {
  width: 60px;
  height: 80px;
  object-fit: cover;
  border-radius: 6px;
  display: block;
  margin: 0 auto;
  box-shadow: 0 1px 4px rgba(60, 30, 90, 0.08);
}
.order-receipt-image {
  width: 48px;
  height: 48px;
  object-fit: cover;
  border-radius: 6px;
  border: 1px solid #e5e7eb;
  display: block;
  margin: 0 auto;
}

.confirm-payment-btn {
  background: linear-gradient(to right, #16a34a, #22d3ee);
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 0.4rem 1rem;
  font-size: 0.98rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
  box-shadow: 0 1px 4px rgba(60, 30, 90, 0.08);
}
.confirm-payment-btn:disabled {
  background: #e5e7eb;
  color: #64748b;
  cursor: not-allowed;
}
.confirm-payment-btn:hover:not(:disabled) {
  background: linear-gradient(to right, #059669, #06b6d4);
  color: #fff;
}

.receipt-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
}
.receipt-modal-content {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.18);
  padding: 1.5rem 1.5rem 1rem 1.5rem;
  position: relative;
  max-width: 95vw;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.receipt-modal-img {
  max-width: 80vw;
  max-height: 70vh;
  border-radius: 8px;
  box-shadow: 0 2px 12px rgba(60, 30, 90, 0.12);
}
.receipt-modal-close {
  position: absolute;
  top: 10px;
  left: 10px;
  background: #e5e7eb;
  color: #c026d3;
  border: none;
  border-radius: 50%;
  width: 2.2rem;
  height: 2.2rem;
  font-size: 1.5rem;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
}
.receipt-modal-close:hover {
  background: #c026d3;
  color: #fff;
}

@media (max-width: 600px) {
  .transfer-info-section {
    margin: 1rem auto;
    max-width: 95vw;
  }

  .transfer-info-box {
    padding: 15px;
    border-radius: 8px;
  }

  .transfer-info-title {
    font-size: 1.1rem;
    margin-bottom: 10px;
  }

  .transfer-number-display {
    padding: 10px 12px;
    margin: 10px 0;
  }

  .transfer-number-display strong {
    font-size: 1.2rem;
  }

  .transfer-info-description {
    font-size: 0.9rem;
  }

  .orders-table-wrapper {
    margin-top: 1rem;
  }
  .orders-table,
  .order-items-table {
    font-size: 0.92rem;
    min-width: 500px;
  }
  .orders-table th,
  .orders-table td,
  .order-items-table th,
  .order-items-table td {
    padding: 0.45rem 0.5rem;
  }
  .orders-title {
    font-size: 1.1rem;
    margin-top: 0.7rem;
  }
  .order-receipt-image {
    width: 38px;
    height: 38px;
  }
  .order-items-table img.order-book-image {
    width: 40px;
    height: 54px;
  }
  .expand-btn,
  .confirm-payment-btn {
    font-size: 0.95rem;
    padding: 0.3rem 0.7rem;
    width: 1.7rem;
    height: 1.7rem;
  }
  .receipt-modal-content {
    padding: 0.7rem 0.3rem 0.3rem 0.3rem;
    max-width: 99vw;
    max-height: 95vh;
  }
  .receipt-modal-img {
    max-width: 95vw;
    max-height: 60vh;
  }
  .receipt-modal-close {
    width: 1.7rem;
    height: 1.7rem;
    font-size: 1.1rem;
    top: 4px;
    left: 4px;
  }
}

@media (max-width: 400px) {
  .orders-table,
  .order-items-table {
    font-size: 0.8rem;
    min-width: 350px;
  }
  .orders-title {
    font-size: 0.95rem;
  }
  .order-receipt-image {
    width: 28px;
    height: 28px;
  }
  .order-items-table img.order-book-image {
    width: 28px;
    height: 38px;
  }
}
